<template>
	<div class=" _full_router fund-manage-con ">
	<custHeader :title="title"></custHeader>
		<div class="fund-manage-contain">
			<mt-field label="币种" placeholder="请选择币种" v-model="currency.name" v-lfoucs="selectTypeVisibleShow" v-remoteoptions="dicts.currency"></mt-field>
			<mt-field label="资金账户" placeholder="请输入资金账户" v-model="fundAcct" ></mt-field>
			<mt-field label="资金密码" placeholder="请输入资金密码" v-model="fundPassword" type="password"></mt-field>
			<mt-field label="银行密码" placeholder="请输入银行密码" v-model="bankPassword"  type="password"></mt-field>
			<mt-field label="转账金额" placeholder="请输入转账金额" v-model="bizAmt"  type="number"></mt-field>
			<mt-button  type="primary" size="large"  @click="upCltBankTran">充值</mt-button>

			 <div>转账流水</div>
				  <ul class="my-hold-container">
				<li>
                <div>操作时间</div>
                <div class="text-right">发生金额</div>
                <div class="text-right">资金账号</div>
                <div class="text-right">状态</div>
                </li>
				    <li v-for="(item,index) in cltBankList">
				  <!--    <router-link :to="{path:'/financial/detail',query:{issNo:item.issNo,stkBiz:stkBizs.subscribe}}"> -->
				      <div><div> {{item.operDate | datetext}}</div></div>
                   <div class="text-right"><div>{{item.bizAmt | toMoney}}</div><div>{{item.fundBln | toMoney}}</div></div>
                   <div class="text-right"><div>{{item.trdacct}}</div></div>
                   <div class="text-right"><div  v-remoteoptionsspan="dicts.bizStatus">{{item.bizStatus}}</div></div>
				   <!--  </router-link> -->
				    </li>
				  </ul>
				<div>资金流水</div>
			<ul class="my-hold-container">
				<li>
                <div>操作时间</div>
                <div class="text-right">发生金额/资金后余额</div>
                <div class="text-right">类型</div>
                <div class="text-right">备注</div>
                </li>
				    <li v-for="(item,index) in cltFundList">
				  <!--    <router-link :to="{path:'/financial/detail',query:{issNo:item.issNo,stkBiz:stkBizs.subscribe}}"> -->
				      <div><div> {{item.opDate | datetext}}</div><div> {{item.opTime | timetext}}</div></div>
                   <div class="text-right"><div>{{item.settAmt | toMoney}}</div><div>{{item.fundBln | toMoney}}</div></div>
                   <div class="text-right"><div>金</div></div>
                   <div class="text-right"><div>{{item.remark}}</div></div>
				   <!--  </router-link> -->
				    </li>
				  </ul>


			<mt-popup
	          v-model="selectTypeVisible"
	          style="width:80%"
	          pop-transtion="popop-fade"
	          class="select-dict"
	        >
          <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="3"></mt-picker>
       		<div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
          	<button class="btn" @click="sureSelectType">确认</button>
          </div>
        </mt-popup>
        </div>
	</div>
</template>
<script>
import {Field,Picker,Popup,MessageBox} from 'mint-ui'
import custHeader from  'components/custHeader'

Vue.component(Field.name,Field)
Vue.component(Popup.name,Popup)
Vue.component(Picker.name,Picker)
export default{
	data(){
		return {
		title:"充值",
		dicts:{
			currency:{
				name:"CURRENCY",
				dataKey:"selectTypeSlots"
			},
			bizStatus:{
				name:"BIZ_STATUS"
			}
		},
		currency:{value:'',name:""},
		fundAcct:"",
		fundPassword:"",
		bankPassword:"",
		bizAmt:"",
		selectTypeVisible:false,
		cltFundList:[],
		cltBankList:[],
		selectTypeSlots:[{
        flex:2,
        values:[{value:'0',name:'人民币2'},{value:'1',name:'美元2'},{value:'2',name:'港币2'}],
      }],
		}
	},
	created:function(){
		//console.log(this.selectTypeSlots);
		this.upCltLogFundQry();
		this.upCltBankTranQry();
	},
	 components:{
    custHeader
  },
	methods:{
		upCltBankTranQry:function(){
				var pars={	sort:"settDate",
							order:"desc",
							offset:0,
							limit:25,
							//bgnDate:'',
							//endDate:'',
						}
				axiosUtil.upCltBankTranQry(pars).then(data=>{
					var data=data.data;
					this.cltBankList=data.rows;
				})

		},
		upCltLogFundQry:function(){
			var pars={	order:"desc",
					limit:5,
					offset:0}
		axiosUtil.upCltLogFundQry(pars).then(data=>{
			var data=data.data;
			this.cltFundList=data.rows;
		})
		},
		selectTypeChange:function(picker,value){
			if(value[0]){
				this.currency=value[0];
			}

		},
		selectTypeVisibleShow:function(){
			this.selectTypeVisible=true;
		},
		dissSelectType:function(){
			this.currency={value:'',name:""};
			this.selectTypeVisible=false;
		},
		sureSelectType:function(){
			if(!this.currency){
		        this.currency = this.selectTypeSlots[0].values[0];
		      }
		      this.selectTypeVisible = false
		},
		upCltBankTran:function(){
			this.selectTypeSlots=[{
        flex:2,
        values:[{value:'0',name:'人民币'},{value:'1',name:'美元'},{value:'2',name:'港币'}],
      }];
      	if(global.isOpenMd5){
        this.bankPassword=global.md5(this.bankPassword);
         this.fundPassword=global.md5(this.fundPassword);
      }
			var pars={
				fundAcct:this.fundAcct,
				bankPassword:this.bankPassword,
				fundPassword:this.fundPassword,
				currency:this.currency.value,
				bankTrdCode:"00",
				bizAmt:this.bizAmt,
			}
			axiosUtil.upCltBankTran(pars).then(data=>{
				var data=data.data;
				if(data.errorcode==1){
					 MessageBox("提示", data.errormsg);
				}else{
					MessageBox("提示", "充值成功");
					this.upCltLogFundQry();
					this.upCltBankTranQry();
				}

			})
		}
	}
}

</script>
<style scoped>
.fund-manage-con{

	height: 100%;
	overflow-y:auto;
}
.fund-manage-contain{
	padding:12px;
}
.select-dict{
	font-size: 14px;
}
	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}
	.my-hold-container{
		background: #fff;
		margin-top: 12px;
	}
</style>



